@import "src/style/z-index";
@import "src/style/palette";
@import "src/style/media-queries.scss";

.wave,
.wave-wrapper {
  --wave-height: 6rem;
  --wave-width: 40rem;

  @include hms {
    @include ms {
      --wave-height: 10rem;
      --wave-width: 60rem;
    }

    @include m {
      --wave-height: 8rem;
      --wave-width: 55rem;
    }
  }
}

.wave-wrapper {
  height: calc(var(--wave-height) - 2px);
}

.wave {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 300%;
  height: calc(var(--wave-height) - 2px);
  background-size: var(--wave-width) var(--wave-height);
}

.wave-wrapper-1 {
  z-index: z-index("waves");
  opacity: 1;
  width: 100%;
  position: absolute;
  bottom: -2px;
}

.wave-wrapper-2 {
  z-index: z-index("waves") - 1;
  opacity: 0.5;
  width: 100%;
  position: absolute;
  bottom: -2px;
}

.wave-wrapper-3 {
  z-index: z-index("waves") - 2;
  opacity: 0.2;
  width: 100%;
  position: absolute;
  bottom: -2px;
}

.wave-wrapper-4 {
  z-index: z-index("waves") - 3;
  opacity: 0.7;
  width: 100%;
  position: absolute;
  bottom: -2px;
}
